import React, { useEffect } from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { ScriptManager } from '@callstack/repack/client';
const AsyncComponent = () => {
  const [data, setData] = React.useState(0);

  const calculate = async () => {
    try {
    const NativeLibDemo = await ScriptManager.shared.loadScript(
      /* webpackChunkName: "react-native-lib-demo" */
      'react-native-lib-demo'
    );
    setData(NativeLibDemo.multiply(3, 4));
    } catch (error) {
      console.log('Manideep Error loading module:', error);
    }
  };
  useEffect(() => {
    calculate();
  }, []);
  return (
    <View style={styles.container}>
      <Text style={styles.text}>The Module return this {data}</Text>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    padding: 20,
    backgroundColor: '#f0f0f0',
    borderRadius: 10,
    marginTop: 20,
    alignItems: 'center',
  },
  text: {
    fontSize: 18,
    color: '#333',
  },
});

export default AsyncComponent;
